import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {BusinessServiceManagementComponent} from './business-service-management.component';
import {BusinessChildModule} from './business-child/business-child.module';


const routes: Routes = [
  {
    path: '',
    component: BusinessServiceManagementComponent,
    children: [
      {
        path: '',
        loadChildren: './business-child/business-child.module#BusinessChildModule'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BusinessServiceManagementRoutingModule {}
